const mutations = {
  userRequest: state => {
    state.status = "loading";
  },
  userSuccess: (state, data) => {
    state.status = "success";
    state.user = data;
  },
  dispatcherSuccess: (state, data) => {
    state.dispatcher = data;
  },
  userError: state => {
    state.status = "error";
  },
  authLogout: state => {
    state.user = {};
    state.dispatcher = {};
  }
}

export default mutations