import axios from '@/axios.js'

const actions = {

  userRequest: ({ commit, dispatch }) => {
    commit("userRequest")

    const getUser = new Promise((resolve) => {
      axios.get('/auth/users/me').then(({ data }) => {
        resolve(data)
      })
    })

    const getDispatcher = new Promise((resolve) => {
      axios.get('/dispatchers/dispatchers/me').then(({ data }) => {
        resolve(data)
      })
    })

    Promise.all([getUser, getDispatcher]).then(values => {
      commit("userSuccess", values[0])
      commit("dispatcherSuccess", values[1])
    }).catch(function (error) {
      console.error(error)
      commit("userError")
      dispatch("authLogout")
    })
  },

  userUpdate: ({ commit, dispatch }, { username }) => {
    axios.patch('/auth/users/me', { username })
    .then(( {data} ) => {
      console.log("userUpdate - " + data)
      dispatch("userRequest")
    })
    .catch(function (error) {
      console.log(error)
    })
  },

  dispatcherUpdate: ({ commit }, { profile }) => {
    axios.patch('/dispatchers/dispatchers/me', {
      profile: { ...profile }
    })
    .then(( {data} ) => {
      console.log("dispatcherUpdate - " + data)
    })
    .catch(function (error) {
      console.log(error)
    })
  }

}

export default actions