const mutations = {
  authRequest: state => {
    state.status = "loading";
  },
  authSuccess: (state, resp) => {
    state.status = "success";
    state.accessToken = resp.accessToken;
    state.refreshToken = resp.refreshToken;
  },
  authError: state => {
    state.status = "error";
  },
  authLogout: state => {
    state.accessToken = "";
    state.refreshToken = "";
  }
}

export default mutations